export default function MigrateV2Pair() {
  const { address } = useParams();
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(resetMintState());
    return () => {
      dispatch(resetMintState());
    };
  }, [dispatch]);
  const account = useAccount();
  const validatedAddress = isAddress(address);
  const pair = usePairContract(validatedAddress ? validatedAddress : void 0);
  const token0AddressCallState = useSingleCallResult(pair, "token0", void 0, NEVER_RELOAD);
  const token0Address = token0AddressCallState?.result?.[0];
  const token1Address = useSingleCallResult(pair, "token1", void 0, NEVER_RELOAD)?.result?.[0];
  const token0 = useToken(token0Address);
  const token1 = useToken(token1Address);
  const liquidityToken = useMemo(
    () => account.chainId && validatedAddress ? new Token(account.chainId, validatedAddress, 18) : void 0,
    [account.chainId, validatedAddress]
  );
  const pairBalance = useTokenBalance(account.address, liquidityToken);
  const totalSupply = useTotalSupply(liquidityToken);
  const [reserve0Raw, reserve1Raw] = useSingleCallResult(pair, "getReserves")?.result ?? [];
  const reserve0 = useMemo(
    () => token0 && reserve0Raw ? CurrencyAmount.fromRawAmount(token0, reserve0Raw) : void 0,
    [token0, reserve0Raw]
  );
  const reserve1 = useMemo(
    () => token1 && reserve1Raw ? CurrencyAmount.fromRawAmount(token1, reserve1Raw) : void 0,
    [token1, reserve1Raw]
  );
  if (!validatedAddress || !pair || pair && token0AddressCallState?.valid && !token0AddressCallState?.loading && !token0AddressCallState?.error && !token0Address) {
    logger.warn("MigrateV2Pair", "MigrateV2Pair", "Invalid pair address", {
      token0Address,
      token1Address,
      chainId: account.chainId
    });
    return <Navigate to="/migrate/v2" replace />;
  }
  return <Trace
    logImpression
    page={InterfacePageNameLocal.MigrateV2Pair}
    properties={{
      pool_address: validatedAddress,
      chain_id: account.chainId,
      label: [token0?.symbol, token1?.symbol].join("/"),
      token0Address,
      token1Address
    }}
  ><BodyWrapper style={{ padding: 24 }}><AutoColumn gap="16px"><AutoRow style={{ alignItems: "center", justifyContent: "space-between" }} gap="8px"><BackArrowLink to="/migrate/v2" /><MigrateHeader><Trans i18nKey="migrate.v2Title" /></MigrateHeader><SettingsTab
    autoSlippage={DEFAULT_MIGRATE_SLIPPAGE_TOLERANCE}
    chainId={account.chainId}
    hideRoutingSettings
  /></AutoRow>{!account.isConnected ? <ThemedText.DeprecatedLargeHeader><Trans i18nKey="migrate.connectAccount" /></ThemedText.DeprecatedLargeHeader> : pairBalance && totalSupply && reserve0 && reserve1 && token0 && token1 ? <V2PairMigration
    pair={pair}
    pairBalance={pairBalance}
    totalSupply={totalSupply}
    reserve0={reserve0}
    reserve1={reserve1}
    token0={token0}
    token1={token1}
  /> : <EmptyState message={<Trans i18nKey="common.loading" />} />}</AutoColumn></BodyWrapper></Trace>;
}
